.app {
  @apply flex flex-col items-center w-full h-full bg-gray-500;

  &__content {
    @apply w-full h-full p-2;
  }
}
.button {
    @apply inline-block font-light py-1.5 px-5 cursor-pointer text-left;

    &--primary {
        @apply bg-purple-500 hover:bg-purple-900 text-white;
    }

    &--secondary {
        @apply bg-green-500 hover:bg-green-900 text-white;
    }

    &--danger {
        @apply bg-red-600 text-white;
    }

    &--upload {
        @apply text-white font-bold py-2 px-4 w-full inline-flex items-center;
    }

    &--disabled {
        @apply bg-opacity-25 pointer-events-none cursor-default;
    }
}
.card {
  @apply p-4 rounded bg-white w-full h-full;

  &__content {
    @apply w-full h-full p-2;
  }

  &__header {
    @apply text-center text-4xl;
  }
}
.form {

  @apply flex flex-col;

  &__field {
    @apply flex flex-col mt-1 mb-1;
  }

  &__input {
    @apply block outline-none rounded-xl py-1.5 px-3 w-full border border-gray-400;
  }

  &__input--upload {
    @apply cursor-pointer absolute block opacity-0 right-0 top-0 h-full w-full;
  }

  &__label {
    @apply block mb-1 uppercase font-medium text-sm;
  }
}
.image-preview {
  @apply w-full;
  &__thumbnail {
    width: 50px;
    height: 50px;
  }

  &__image-name {
    @apply text-sm;
  }
}
.output {
  &__video {
    @apply max-h-96 max-w-7xl;
  }
}
@tailwind base;
@tailwind components;
@tailwind utilities;
@import "src/styles/components/_app.scss";
@import "src/styles/components/_button.scss";
@import "src/styles/components/_image-preview.scss";
@import "src/styles/components/_form.scss";
@import "src/styles/components/_card.scss";

body, html, #root {
  width: 100%;
  height: 100%;
